@font-face {
	font-family: "Roboto300";
	src: url('../../assets/Roboto300.ttf')
}
.userProfile {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.userMenu {
    width: 64px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
.nav {
    position: absolute;
    top: 45px;
    right: 38px;
    width: 11px;
    height: 11px;
    background-color: #FFFFFF;
    transform: rotate(45deg);
}
.dropMenu {
    position: relative;
    width: 120px;
    height: 46px;
    margin-top: 10px;
    padding: 7px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: #FFFFFF;
    border-radius: 5%;
    z-index: 100;
}
.dropMenu a {
    font-family: "Roboto300";
    font-weight: bold;
    line-height: 16px;
    font-size: 14px;
    text-decoration: none;
    color: #000000;
}
.dropMenu a:hover {
    color: #0067A3;
    text-decoration-line: underline;
}
.userAvatar {
    width: 40px;
    height: 40px;
    background-color: #FFFFFF;
    border-radius: 50%;
}
.arrowDown {
    width: 12px;
    height: 7.42px;
}
.arrowUp {
    width: 12px;
    height: 7.42px;
    transform: rotate(180deg);
}